@font-face {
    font-family: 'btd6font';
    src: url('../static/LuckiestGuy-Regular.ttf');
}

/* CSS for including a full height background image*/
html{
    height: 100%!important;
    background-image: url('../static/backgrounds/btd6_background.png');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 100% 100%;
}

body {
    background-color: transparent!important;
}
.btd6font {
    font-family: 'btd6font';
}

div.transbox {
    background-color: rgba(255,255,255,0.65);
    border: 1px solid black;
}

.scrollable-columns {
    overflow-y: scroll;
}

.cleanlink {
    color: #000000;
    text-decoration: none;
}

.cleanlink:hover {
    color: #000000;
    text-shadow: 0 0 5px #000000;
}
